.graph-holder {
    display: grid;
    position: absolute;
    top: 0px;
    left: 0px;
    grid-template-rows: 64px auto 64px;
    width: 100%;
    height: 100%;
}

.graph-holder>div {
    overflow: hidden;
}

@media screen and (max-width: 1000px) {
    .graph-holder>h1 {
        font-size: 8vw;
    }
}